<template>
  <div class="dialog-container">
    <el-dialog :title="questionTitle" width="800px" top="30px" :visible.sync="visible" :close-on-click-modal="false" @close="$emit('update:show', false)" :show="show">
      <div class="Question">
        <el-form style="width:750px;margin:0 auto" class="demo-ruleForm" :model="question" :inline="true" label-width="125px">
          <el-form-item label="" required v-if="chooseableItem">
            <el-input size="mini" type="text" style="width:520px;" v-model="xmmc" readonly placeholder="">
              <el-button slot="append" icon="el-icon-circle-plus-outline" style="width:30px;padding:0 12px;" @click="addQuestiontItem"></el-button>
            </el-input>
          </el-form-item>
          <el-form-item label="" required v-if="!chooseableItem">
            <el-input size="mini" type="text" style="width:520px" v-model="xmmc" readonly></el-input>
          </el-form-item>
          <el-form-item label="" required v-if="(showCondition==1||showCondition==2)">
            <el-select v-model="question.wtlb" size="mini" placeholder="" style="width:192px">
              <el-option v-for="(item,index) in wtlb" :key="index" :label="item.mc" :value="item.label">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" required>
            <el-select v-model="question.sfjj" size="mini" placeholder="" style="width:192px">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" required v-if="(showCondition==1||showCondition==2)">
            <el-select v-model="question.wtjb" size="mini" placeholder="" style="width:192px">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" required>
            <el-select v-model="question.cp" size="mini" placeholder="" style="width:192px" @change="handleChangeCp">
              <el-option v-for="(cp,index) in xmcpList" :key="index" :label="cp.mc" :value="cp.label">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" required v-if="(showCondition==1||showCondition==2)">
            <el-select v-model="question.yxfw" size="mini" placeholder="" style="width:192px">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="bug" required v-if="(showCondition==1||showCondition==2)">
            <el-select v-model="question.sfbug" size="mini" placeholder="bug" style="width:192px">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" required v-if="(showCondition==1||showCondition==2)">
            <el-input size="mini" v-model="question.bbh" type="text" placeholder="" style="width:193px;"></el-input>
          </el-form-item>

          <el-form-item label="" required v-if="!accreditShow">
            <el-date-picker :picker-options="pickerBeginDateQw" :clearable="false" size="mini" v-model="question.qwjjrq" type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd" style="width:193px;"></el-date-picker>
          </el-form-item>
          <el-form-item label="" required v-if="accreditShow">
            <el-input size="mini" style="width:193px;" readonly v-model="question.qwjjrqO"></el-input>
          </el-form-item>

          <el-form-item label="" v-if="isJZuser != 1">
            <el-upload style="width:510px;" class="upload-demo" ref="upload" :limit='1' :action="uploadAction" :before-upload="beforeUpload" :on-remove="handleRemove" :on-change="handleChange" :file-list="fileList" :show-file-list="true" :auto-upload="false">
              <el-button slot="trigger" size="small" type="primary"></el-button>
            </el-upload>
          </el-form-item>
          <el-form-item label="" required v-if="accreditShow">
            <el-date-picker :picker-options="pickerBeginDateBefore" :clearable="false" size="mini" v-model="question.cnjsrq" type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd" style="width:193px;"></el-date-picker>
          </el-form-item>

          <el-form-item label="" required v-if="!accreditShow">
            <el-input size="mini" v-model="question.title" type="text" placeholder="" style="width:520px;"></el-input>
          </el-form-item>
        </el-form>
        <div class="question-textarea" v-if="!accreditShow">
          <p></p>
          <div id="summernote"> </div>
        </div>

        <div style="text-align:right;width:100%;margin:10px 0;padding:0 20px;">
          <el-button size="small" type="primary" @click="handleCommit" v-if="!accreditShow"></el-button>
          <el-button size="small" type="primary" @click="handleAccredit" v-if="accreditShow"></el-button>
          <el-button size="small" type="info" @click="handleCancel"></el-button>
        </div>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogQuestionVisible" :close-on-click-modal="false" width="800px" top="30px" append-to-body>
      <div style="padding:10px;">
        <itemChoose @handleEdit="handleEdit"></itemChoose>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import itemChoose from "@/components/BusinessPage/itemChoose.vue";
import {
  saveQuestion,
  customerQuestion,
  showQuestionCondition,
  queryResponsibleProduct,
  queryProductSolutionTime
} from "@/api/xmkb.js";
import {
  formatTime,
  getMenu,
  getSession,
  getMyDate,
  GetDateStr
} from "@/utils/util.js";
export default {
  data() {
    return {
      dialogQuestionVisible: false,
      visible: this.show,
      xmmc: "",
      xmbh: "",
      showCondition: "",
      question: {
        wtlb: "",
        sfjj: "",
        wtjb: "",
        cp: "",
        yxfw: "",
        sfbug: "",
        bbh: "",
        title: "",
        nr: "",
        wid: "",
        qwjjrq: "",
        qwjjrqO: "",
        cnjsrq: ""
      },
      cplist: [],
      wtlb: [],
      uploadAction: "https://jsonplaceholder.typicode.com/posts/",
      fileList: [],
      fileData: [],
      filesData: [],
      pickerBeginDateBefore: this.handleFocusDate(),
      pickerBeginDateQw: {
        disabledDate(time) {
          let curDate = new Date().getTime();
          let three = 30 * 24 * 3600 * 1000;
          let threeMonths = curDate + three;
          return (
            time.getTime() < Date.now() - 8.64e7 || time.getTime() > threeMonths
          );
        }
      },
      isJZuser: null,
      xmcpList: [],
      xmData:[] //
    };
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    questionTitle: {
      type: String,
      default: ""
    },
    accreditShow: {
      type: Boolean,
      default: false
    },
    wid: {
      type: String,
      default: ""
    },
    slContent: {
      type: String,
      default: ""
    },
    questionInfo: {
      type: Object,
      default: function() {
        return {};
      }
    },
    chooseableItem: {
      type: Boolean,
      default: true
    },
    itemInfo: {
      type: Object,
      default: function() {
        return {};
      }
    },
    guid: {
      type: String,
      default: ""
    }
  },
  components: { itemChoose },
  methods: {
    handleFocusDate() {
      let self = this;
      return {
        disabledDate(time) {
          let curDate = new Date().getTime();
          let delayHs = JSON.parse(self.questionInfo.cphs) * 24 * 3600 * 1000;
          let delayDate = curDate + delayHs;
          return (
            time.getTime() < Date.now() - 8.64e7 || time.getTime() > delayDate
          );
        }
      };
    },
    handleEdit(data) {
      this.xmData = data;
      // //
      // if(data.gcfwzt == '0'){
      //   this.$alert(data.gcfwztsm,'',{type:'warning',confirmButtonText: ''});
      //   return;
      // }
      if (this.questionTitle == "") {
        this.queryResponsibleProduct(data.xmbh);
      }
      this.xmbh = data.xmbh;
      this.xmmc = data.xmmc;
      this.dialogQuestionVisible = false;
    },
     //
    addQuestiontItem() {
      this.dialogQuestionVisible = true;
    },
    handleCancel() {
      //dialog
      this.visible = false;
    },

    // 9.26
    handleChangeCp(val) {
      this.$get(this.API.questionLimitProduct, {
        xmbh: this.xmbh,
        cpbh: val
      }).then(res => {
        if (res.state == "success") {
          let cpmc = "";
          this.xmcpList.forEach(ele => {
            if (ele.label == val) cpmc = ele.mc;
          });
          if (!res.data) {
            this.$alert(" " + cpmc + " ,",
            "",
              {
                confirmButtonText: "",
                type: "warning"
              }
            );
          }
        }
      });
      if (!!this.accreditShow) {
        queryProductSolutionTime({ cpbh: val }).then(({ data }) => {
          if (data.state == "success") {
            // this.question.cnjsrq = GetDateStr(data.data);
            this.questionInfo.cphs = data.data;
            this.question.cnjsrq = "";
          }
        });
      }
    },
    handleAccredit() {
      //
      if (
        !this.xmbh ||
        !this.question.wtlb ||
        !this.question.sfjj ||
        !this.question.wtjb ||
        !this.question.cp ||
        !this.question.yxfw ||
        !this.question.sfbug ||
        !this.question.bbh ||
        !this.question.cnjsrq
      ) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning",
          callback: action => {}
        });
        return;
      }
      this.$confirm(
        ":" +
          this.questionInfo.qwjjrq +
          ":" +
          this.question.cnjsrq +
          ",",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }
      )
        .then(() => {
          customerQuestion({
            wtlb: this.question.wtlb,
            jjyf: this.question.sfjj,
            wtjb: this.question.wtjb,
            cpbh: this.question.cp,
            yxfw: this.question.yxfw,
            sfbg: this.question.sfbug,
            bbh: this.question.bbh,
            qwjjrq: this.questionInfo.qwjjrq,
            cnjsrq: this.question.cnjsrq,
            xmmc: this.xmmc,
            xmbh: this.xmbh,
            wid: this.wid,
            Guid: this.guid,
            hjfjwid:
              this.fileData.length == 0 ? "" : this.fileData[0].split("|")[0],
            nr: this.slContent //
          }).then(({ data }) => {
            if (data.state == "success") {
              this.visible = false;
              this.$alert("", "", {
                confirmButtonText: "",
                type: "success",
                callback: action => {
                  this.$emit("handleSLsuccess", "");
                }
              });
            } else {
              this.$alert(data.msg, "", {
                confirmButtonText: "",
                type: "error",
                callback: action => {}
              });
            }
          });
        })
        .catch(() => {});
    },
    
    //
    handleCommit() {
      this.question.nr = $("#summernote").summernote("code");
      if (this.showCondition == 1 || this.showCondition == 2) {
        if (this.xmmc == "") {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        } else if (
          this.question.wtlb == "" ||
          this.question.sfjj == "" ||
          this.question.wtjb == "" ||
          this.question.cp == "" ||
          this.question.yxfw == "" ||
          this.question.sfbug == "" ||
          this.question.bbh == "" ||
          this.question.qwjjrq == "" ||
          this.question.title == "" ||
          this.question.nr == "" ||
          this.question.nr == "<p><br></p>"
        ) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        }
        saveQuestion({
          wtlb: this.question.wtlb,
          jjyf: this.question.sfjj,
          wtjb: this.question.wtjb,
          cpbh: this.question.cp,
          yxfw: this.question.yxfw,
          sfbg: this.question.sfbug,
          bbh: this.question.bbh,
          bt: this.question.title,
          qwjjrq: this.question.qwjjrq,
          xmmc: this.xmmc,
          xmbh: this.xmbh,
          nr: this.question.nr,
          hjfjwid:
            this.fileData.length == 0 ? "" : this.fileData[0].split("|")[0],
          wid: this.question.wid
        }).then(({ data }) => {
          if (data.state == "success") {
            this.visible = false;
            (this.fileList = []),
              (this.fileData = []),
              (this.filesData = []),
              this.$alert("", "", {
                confirmButtonText: "",
                type: "success",
                callback: action => {
                  this.$emit("handleTWsuccess", "");
                }
              });
          } else {
            this.$alert(data.msg + ",!", "", {
              confirmButtonText: "",
              type: "error"
            });
          }
        });
      } else if (this.showCondition == 0) {
        // 
        if (
          this.question.title == "" ||
          this.question.nr == "" ||
          this.question.cp == "" ||
          this.question.qwjjrq == "" ||
          this.question.sfjj == "" ||
          this.question.xmbh == ""
        ) {
          this.$alert("!", "", {
            confirmButtonText: "",
            type: "warning"
          });
          return;
        }
        customerQuestion({
          bt: this.question.title,
          nr: this.question.nr,
          wid: this.question.wid,
          cpbh: this.question.cp,
          qwjjrq: this.question.qwjjrq,
          hjfjwid:
            this.fileData.length == 0 ? "" : this.fileData[0].split("|")[0],
          jjyf: this.question.sfjj,
          xmmc: this.xmmc, //
          xmbh: this.xmbh
        }).then(({ data }) => {
          if (data.state == "success") {
            this.visible = false;
            this.$alert("", "", {
              confirmButtonText: "",
              type: "success",
              callback: action => {
                this.$emit("handleTWsuccess", "");
              }
            });
          } else {
            this.$alert(data.msg, "", {
              confirmButtonText: "",
              type: "error"
            });
          }
        });
      }
    },
    // 
    handleRemove(file, fileList) {
      this.fileData = [];
      this.fileList = [];
      this.filesData = [];
    },

    beforeUpload(file) {
      let fd = new FormData();
      fd.append("fileUpload", file);
      fd.append("xmbh", this.xmbh);

      axios
        .post(window.baseurl + "attachment/uploadAttach.do", fd, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(res => {
          if (res.data.state == "success") {
            this.fileData.push(res.data.data); // list Arr
          } else {
            this.$alert(res.data.msg, "", {
              confirmButtonText: ""
            });
          }
        });
      return true;
    },

    handleChange(file, fileList) {
      let r = [];
      this.filesData.push(file);
      for (var i = 0, l = this.filesData.length; i < l; i++) {
        for (var j = i + 1; j < l; j++)
          if (this.filesData[i].name === this.filesData[j].name) j = ++i;
        r.push(this.filesData[i]);
      }
      this.fileList = r;
      this.$refs.upload.submit();
    },
    // 
    queryResponsibleProduct(xmbh) {
      this.xmcpList = [];
      queryResponsibleProduct({
        xmbh: xmbh
      }).then(res => {
        if (res.data.state == "success") {
          // 2018.12.18 
          if (JSON.stringify(res.data.data) == "{}") {
            let xmjlxm = !this.itemInfo.yfzrrxm?this.xmData.yfzrrxm:this.itemInfo.yfzrrxm
            this.$alert("  ( " + xmjlxm +")", "", {
              confirmButtonText: "",
              type: "warning"
            });
          } else {
            let Arr = Object.keys(res.data.data);
            let McArr = Object.values(res.data.data);
            for (var i = 0; i < Arr.length; i++) {
              this.xmcpList.push({
                label: Arr[i],
                mc: McArr[i]
              });
            }
          }
        }
      });
    }
  },
  watch: {
    show() {
      this.visible = this.show;
      if (this.show) {
        this.$nextTick(() => {
          $("#summernote").summernote({
            dialogsInBody: true,
            placeholder: "",
            focus: true,
            height: 200,
            width: 100 + "%",
            minHeight: 200,
            maxHeight: 200,
            lang: "zh-CN",
            toolbar: [
              ["style", ["bold", "italic", "underline", "clear"]],
              ["font", ["strikethrough", "superscript", "subscript"]],
              ["fontsize", ["fontsize"]],
              ["color", ["color"]],
              ["para", ["ul", "ol", "paragraph"]],
              ["height", ["height"]],
              ["picture"],
              ["link", ["linkDialogShow", "unlink"]]
            ]
          });
        });
        showQuestionCondition().then(({ data }) => {
          //
          this.showCondition = data.data;
        });
        if (!getSession("ProblemType") || !getSession("kycp")) {
          getMenu("ProblemType", this.wtlb, "");
          getMenu("kycp", this.cplist, true);
        } else {
          this.wtlb = getSession("ProblemType");
          this.cplist = getSession("kycp");
        }
        this.fileList = [];
        this.fileData = [];
        this.filesData = [];
        if (this.questionTitle == "") {
          this.xmcpList = [];
          this.question.wtlb = "";
          this.question.sfjj = "";
          this.question.wtjb = "";
          this.question.cp = "";
          this.question.yxfw = "";
          this.question.sfbug = "";
          this.question.bbh = "";
          this.question.title = "";
          this.question.wid = "";
          this.question.nr = "";
          $("#summernote").summernote("code", "");
          if (this.chooseableItem) {
            this.xmmc = "";
            this.xmbh = "";
          } else {
            this.xmmc = this.itemInfo.xmmc;
            this.xmbh = this.itemInfo.xmbh;
            this.queryResponsibleProduct(this.xmbh);
          }
        } else {
          this.xmcpList = this.cplist;
          this.question.title = this.questionInfo.bt;
          this.question.cp = this.questionInfo.cpbh;
          this.question.wtlb = this.questionInfo.wtlb;
          this.question.sfjj = this.questionInfo.jjyf;
          this.question.wtjb = this.questionInfo.wtjb;
          this.question.yxfw = this.questionInfo.yxfw;
          this.question.sfbug = this.questionInfo.sfbg;
          this.question.bbh = this.questionInfo.bbh;
          this.question.qwjjrqO = this.questionInfo.qwjjrqO;
          this.question.cnjsrq =
          this.questionInfo.cnjsrq == ""? this.questionInfo.qwjjrqO: this.questionInfo.cnjsrqO;
          this.question.qwjjrq = this.questionInfo.qwjjrq;
          this.question.wid = this.questionInfo.wid;
          this.xmmc = this.questionInfo.xmmc;
          this.xmbh = this.questionInfo.xmbh;
          this.$nextTick(() => {
            $("#summernote").summernote("code", this.questionInfo.nr);
          });
        }
      }
    }
  }
};
</script>
<style scoped>
.Question {
  padding: 10px 0;
}
div.el-form-item {
  margin-bottom: 8px !important;
}
.question-textarea {
  width: 750px;
  margin: 0 auto;
}
.question-textarea > p {
  width: 125px;
  text-align: right;
  padding-right: 12px;
  font-weight: 700;
}
.question-textarea > p::before {
  content: "*";
  color: #f56c6c;
  margin-right: 4px;
}
</style>



